import Vue from 'vue';
import { Card, Table, TableColumn, Tag } from 'element-ui';
import PageHeaderLayout from 'layouts/PageHeaderLayout/index.vue';
import DescriptionList from 'components/DescriptionList';
import Description from 'components/Description';
import Divider from 'components/Divider';
Vue.use(Card);
Vue.use(Table);
Vue.use(TableColumn);
Vue.use(Tag);
Vue.use(DescriptionList);
Vue.use(Description);
Vue.use(Divider);
export default Vue.extend({
    components: {
        PageHeaderLayout
    },
    data() {
        return {
            loading: false
        };
    },
    computed: {
        basicGoods() {
            return this.$store.state.profile.basicGoods;
        },
        basicProgress() {
            return this.$store.state.profile.basicProgress;
        },
        goodsData() {
            const basicGoods = this.basicGoods;
            let goodsData = [];
            if (basicGoods.length) {
                let num = 0;
                let amount = 0;
                basicGoods.forEach(item => {
                    num += Number(item.num);
                    amount += Number(item.amount);
                });
                goodsData = basicGoods.concat({
                    id: '总计',
                    num,
                    amount
                });
            }
            return goodsData;
        }
    },
    mounted() {
        this.$store.dispatch('profile/fetchBasic');
    }
});



